.LearningOutcomesOuter {
  display: flex;
  width: 832px;
  padding: 24px;
  height: 162px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 8px;
  flex-shrink: 0;
  border: 2px solid;
  border-radius: 6px;
  border-top: 8px solid #f42684;
  border-left: 8px solid #f42684;
  background: #f2f2f2;
}
.LearningOutcomesParent {
  display: flex;
  padding: 8px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
  margin-top: -5px;
}
.LearningOutcomesSection {
  display: flex;
  width: 940px;
  padding: 8px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
.LearningOutcomesTextBox {
  width: 226px;
  height: 36px;
  flex-shrink: 0;
  border-radius: 18px;
  border: 2px solid black;
  background: #fff;
  position: relative;
  top: 30px;
}
.LearningOutcomesText {
  color: #00262b;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px; /* 133.333% */
}
.Welcome {
  border: 1px solid #e5e5e5;
}
